footer {
  background: var(--color-primary);
  padding: 2rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 6rem;
}
footer a {
  color: var(--color-bg);
}
.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
  display: inline-block;
}
.parmalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 2rem;
}
.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}
.footer__socials a {
  background: var(--color-bg);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
}
.footer__socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}
.footer__copyright {
  margin-bottom: 2rem;
  color: var(--color-bg);
}

/*........................................*/
@media screen and (max-width: 600px) {
  .parmalinks {
    flex-direction: column;
    gap: 1.2rem;
  }
  .footer__socials{
    margin-bottom: 1.3rem;
  }
}
